<template>
	<view class="container">
		<!-- È°µÈù¢Ê†áÈ¢ò -->
		<view class="page-header">
			<text class="page-title">{{isEditMode ? 'ÁºñËæëËÆ∞ÂΩï' : 'ËÆ∞‰∏ÄÁ¨î'}}</text>
		</view>
		
		<!-- Á±ªÂûãÂàáÊç¢ -->
		<view class="type-tabs">
			<view class="tab-item" :class="{active: currentType == 'expense'}" @click="switchType('expense')">
				<text class="tab-text">ÊîØÂá∫</text>
			</view>
			<view class="tab-item" :class="{active: currentType == 'income'}" @click="switchType('income')">
				<text class="tab-text">Êî∂ÂÖ•</text>
			</view>
		</view>
		
		<!-- ÈáëÈ¢ùËæìÂÖ• -->
		<view class="amount-section">
			<text class="amount-symbol">¬•</text>
			<input class="amount-input" 
				   type="digit" 
				   v-model="amount" 
				   placeholder="0.00" 
				   @input="onAmountInput"/>
		</view>
		
		<!-- ÂàÜÁ±ªÈÄâÊã© -->
		<view class="category-section">
			<text class="section-title">ÈÄâÊã©ÂàÜÁ±ª</text>
			<view class="category-grid">
				<view v-for="category in currentCategories" 
					  :key="category.id" 
					  class="category-item" 
					  :class="{active: selectedCategory && selectedCategory.id == category.id}"
					  @click="selectCategory(category)">
					<text class="category-icon">{{category.icon}}</text>
					<text class="category-name">{{category.name}}</text>
				</view>
			</view>
		</view>
		
		<!-- Êó∂Èó¥ÈÄâÊã© -->
		<view class="time-section">
			<text class="section-title">Êó∂Èó¥</text>
			<view class="time-container">
				<!-- Êó•ÊúüÈÄâÊã© -->
				<picker class="date-picker" mode="date" :value="dateValue" @change="onDateChange" :start="getMinDate().split(' ')[0]" :end="getMaxDate().split(' ')[0]">
					<view class="time-picker">
						<text class="time-text">{{dateValue || 'ÈÄâÊã©Êó•Êúü'}}</text>
						<text class="time-arrow">üìÖ</text>
					</view>
				</picker>
				
				<!-- Êó∂Èó¥ÈÄâÊã© -->
				<picker class="time-picker-only" mode="time" :value="timeValue" @change="onTimeChange">
					<view class="time-picker">
						<text class="time-text">{{timeValue || 'ÈÄâÊã©Êó∂Èó¥'}}</text>
						<text class="time-arrow">üïê</text>
					</view>
				</picker>
			</view>
		</view>
		
		<!-- Â§áÊ≥®ËæìÂÖ• -->
		<view class="note-section">
			<text class="section-title">Â§áÊ≥®</text>
			<textarea class="note-input" 
					  v-model="note" 
					  placeholder="Ê∑ªÂä†Â§áÊ≥®‰ø°ÊÅØÔºàÂèØÈÄâÔºâ" 
					  maxlength="100"/>
		</view>
		
		<!-- ‰øùÂ≠òÊåâÈíÆ -->
		<view class="save-section">
			<button class="save-btn" @click="saveRecord" :disabled="!canSave">
				{{isEditMode ? 'Êõ¥Êñ∞ËÆ∞ÂΩï' : '‰øùÂ≠òËÆ∞ÂΩï'}}
			</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				currentType: 'expense',
				amount: '',
				selectedCategory: null,
				datetime: '',
				dateValue: '',
				timeValue: '',
				note: '',
				expenseCategories: [],
				incomeCategories: [],
				isEditMode: false, // ÊòØÂê¶‰∏∫ÁºñËæëÊ®°Âºè
				editingRecordId: null, // Ê≠£Âú®ÁºñËæëÁöÑËÆ∞ÂΩïID
				// ÈáëÈ¢ùËÆ°ÁÆóÂ∑•ÂÖ∑ÂáΩÊï∞ÔºåËß£ÂÜ≥ÊµÆÁÇπÊï∞Á≤æÂ∫¶ÈóÆÈ¢ò
				moneyCalculator: {
					// ÈáëÈ¢ùÂä†Ê≥ï
					add(a, b) {
						const factor = 100
						return Math.round((parseFloat(a) * factor + parseFloat(b) * factor)) / factor
					},
					
					// ÈáëÈ¢ùÂáèÊ≥ï
					subtract(a, b) {
						const factor = 100
						return Math.round((parseFloat(a) * factor - parseFloat(b) * factor)) / factor
					},
					
					// ÈáëÈ¢ù‰πòÊ≥ï
					multiply(a, b) {
						const factor = 100
						return Math.round(parseFloat(a) * parseFloat(b) * factor) / factor
					},
					
					// ÈáëÈ¢ùÈô§Ê≥ï
					divide(a, b) {
						if (parseFloat(b) === 0) return 0
						const factor = 100
						return Math.round((parseFloat(a) / parseFloat(b)) * factor) / factor
					},
					
					// Ê†ºÂºèÂåñÈáëÈ¢ùÔºå‰øùÁïô‰∏§‰ΩçÂ∞èÊï∞
					format(amount) {
						return parseFloat(amount).toFixed(2)
					}
				}
			}
		},
		
		computed: {
			currentCategories() {
				return this.currentType == 'expense' ? this.expenseCategories : this.incomeCategories
			},
			
			canSave() {
				return this.amount && parseFloat(this.amount) > 0 && this.selectedCategory
			}
		},
		
		onLoad(options) {
			// Âä†ËΩΩÂàÜÁ±ªÊï∞ÊçÆ
			this.loadCategories()
			
			// Ê£ÄÊü•ÊòØÂê¶‰∏∫ÁºñËæëÊ®°Âºè
			if (options && options.editId) {
				this.isEditMode = true
				this.editingRecordId = options.editId
				// ËÆæÁΩÆÂØºËà™Ê†áÈ¢ò
				uni.setNavigationBarTitle({
					title: 'ÁºñËæëËÆ∞ÂΩï'
				})
				this.loadEditRecord(options.editId)
			} else {
				// Êñ∞Â¢ûÊ®°ÂºèÔºöËÆæÁΩÆÈªòËÆ§Êó∂Èó¥‰∏∫ÂΩìÂâçÊó∂Èó¥
				uni.setNavigationBarTitle({
					title: 'ËÆ∞‰∏ÄÁ¨î'
				})
				const now = new Date()
				this.dateValue = this.formatDateForPicker(now)
				this.timeValue = this.formatTimeForPicker(now)
				this.datetime = this.formatDatetimeForPicker(now)
				console.log('ÂàùÂßãÂåñÊó•Êúü:', this.dateValue, 'Êó∂Èó¥:', this.timeValue)
			}
			
			// Â§ÑÁêÜÈ¢ÑËÆæÂàÜÁ±ªÂèÇÊï∞Ôºà‰ªÖÂú®Êñ∞Â¢ûÊ®°Âºè‰∏ãÊúâÊïàÔºâ
			if (!this.isEditMode && options && options.presetCategory) {
				try {
					const presetCategory = JSON.parse(decodeURIComponent(options.presetCategory))
					console.log('È¢ÑËÆæÂàÜÁ±ª:', presetCategory)
					
					// ËÆæÁΩÆÁ±ªÂûã
					this.currentType = presetCategory.type
					
					// Âª∂ËøüËÆæÁΩÆÂàÜÁ±ªÔºåÁ°Æ‰øùÂàÜÁ±ªÂàóË°®Â∑≤ÁªèËÆ°ÁÆóÂÆåÊàê
					this.$nextTick(() => {
						const category = this.currentCategories.find(cat => cat.id == presetCategory.id)
						if (category) {
							this.selectedCategory = category
						}
					})
				} catch (error) {
					console.error('Ëß£ÊûêÈ¢ÑËÆæÂàÜÁ±ªÂèÇÊï∞Â§±Ë¥•:', error)
				}
			}
		},
		
		methods: {
			// Âä†ËΩΩÂàÜÁ±ªÊï∞ÊçÆ
			loadCategories() {
				// ÈªòËÆ§ÂàÜÁ±ªÊï∞ÊçÆ
				const defaultExpenseCategories = [
					{id: 1, name: 'È§êÈ•Æ', icon: 'üçΩÔ∏è'},
					{id: 2, name: '‰∫§ÈÄö', icon: 'üöó'},
					{id: 3, name: 'Ë¥≠Áâ©', icon: 'üõçÔ∏è'},
					{id: 4, name: 'Â®±‰πê', icon: 'üé¨'},
					{id: 5, name: '‰ΩèÊàø', icon: 'üè†'},
					{id: 6, name: 'ÂåªÁñó', icon: 'üíä'},
					{id: 7, name: 'ÊïôËÇ≤', icon: 'üìö'},
					{id: 8, name: 'ÈÄöËÆØ', icon: 'üì±'},
					{id: 9, name: 'ÊúçË£Ö', icon: 'üëï'},
					{id: 10, name: 'ÂÖ∂‰ªñ', icon: 'üì¶'}
				]
				
				const defaultIncomeCategories = [
					{id: 11, name: 'Â∑•ËµÑ', icon: 'üí∞'},
					{id: 12, name: 'Â•ñÈáë', icon: 'üéÅ'},
					{id: 13, name: 'ÊäïËµÑ', icon: 'üìà'},
					{id: 14, name: 'ÂÖºËÅå', icon: 'üíº'},
					{id: 15, name: 'Á∫¢ÂåÖ', icon: 'üßß'},
					{id: 16, name: 'ÈÄÄÊ¨æ', icon: '‚Ü©Ô∏è'},
					{id: 17, name: 'ÂÖ∂‰ªñ', icon: 'üíé'}
				]
				
				// ‰ªéÊú¨Âú∞Â≠òÂÇ®Âä†ËΩΩÔºåÂ¶ÇÊûúÊ≤°ÊúâÂàô‰ΩøÁî®ÈªòËÆ§Êï∞ÊçÆ
				let expenseCategories = uni.getStorageSync('expenseCategories')
				let incomeCategories = uni.getStorageSync('incomeCategories')
				
				if (!expenseCategories || expenseCategories.length == 0) {
					expenseCategories = defaultExpenseCategories
					uni.setStorageSync('expenseCategories', expenseCategories)
				}
				
				if (!incomeCategories || incomeCategories.length == 0) {
					incomeCategories = defaultIncomeCategories
					uni.setStorageSync('incomeCategories', incomeCategories)
				}
				
				this.expenseCategories = expenseCategories
				this.incomeCategories = incomeCategories
			},
			
			// Âä†ËΩΩÁºñËæëÁöÑËÆ∞ÂΩïÊï∞ÊçÆ
			loadEditRecord(recordId) {
				console.log('Âä†ËΩΩÁºñËæëËÆ∞ÂΩïID:', recordId)
				const records = uni.getStorageSync('records') || []
				const record = records.find(item => item.id == recordId)
				
				if (record) {
					// Âä†ËΩΩËÆ∞ÂΩïÊï∞ÊçÆÂà∞Ë°®Âçï
					this.currentType = record.type
					this.amount = record.amount
					this.note = record.note || ''
					
					// ËÆæÁΩÆÊó∂Èó¥
					const recordDate = new Date(record.time)
					this.dateValue = this.formatDateForPicker(recordDate)
					this.timeValue = this.formatTimeForPicker(recordDate)
					this.datetime = this.formatDatetimeForPicker(recordDate)
					
					// ËÆæÁΩÆÂàÜÁ±ªÔºàÈúÄË¶ÅÁ≠âÂæÖÂàÜÁ±ªÊï∞ÊçÆÂä†ËΩΩÂÆåÊàêÔºâ
					this.$nextTick(() => {
						const category = this.currentCategories.find(cat => cat.id == record.categoryId)
						if (category) {
							this.selectedCategory = category
						} else {
							// Â¶ÇÊûúÊâæ‰∏çÂà∞ÂØπÂ∫îÂàÜÁ±ªÔºåÂèØËÉΩÊòØÂàÜÁ±ªÂ∑≤Ë¢´Âà†Èô§ÔºåÂàõÂª∫‰∏Ä‰∏™‰∏¥Êó∂ÂàÜÁ±ªÂØπË±°
							this.selectedCategory = {
								id: record.categoryId,
								name: record.categoryName,
								icon: record.categoryIcon
							}
						}
					})
					
					console.log('Âä†ËΩΩÁºñËæëËÆ∞ÂΩï:', record)
				} else {
					console.error('Êâæ‰∏çÂà∞Ë¶ÅÁºñËæëÁöÑËÆ∞ÂΩï:', recordId)
					uni.showToast({
						title: 'ËÆ∞ÂΩï‰∏çÂ≠òÂú®',
						icon: 'error'
					})
					// ËøîÂõû‰∏ä‰∏ÄÈ°µ
					setTimeout(() => {
						uni.navigateBack()
					}, 1500)
				}
			},
			
			switchType(type) {
				this.currentType = type
				this.selectedCategory = null // ÂàáÊç¢Á±ªÂûãÊó∂Ê∏ÖÈô§Â∑≤ÈÄâÂàÜÁ±ª
			},
			
			onAmountInput(e) {
				let value = e.detail.value
				// Á°Æ‰øùÂè™ÊúâÊï∞Â≠óÂíåÂ∞èÊï∞ÁÇπ
				value = value.replace(/[^\d.]/g, '')
				// Á°Æ‰øùÂè™Êúâ‰∏Ä‰∏™Â∞èÊï∞ÁÇπ
				const dotIndex = value.indexOf('.')
				if (dotIndex !== -1) {
					value = value.slice(0, dotIndex + 1) + value.slice(dotIndex + 1).replace(/\./g, '')
					// ÈôêÂà∂Â∞èÊï∞ÁÇπÂêé‰∏§‰Ωç
					if (value.length > dotIndex + 3) {
						value = value.slice(0, dotIndex + 3)
					}
				}
				this.amount = value
			},
			
			selectCategory(category) {
				this.selectedCategory = category
			},
			
			onDatetimeChange(e) {
				console.log('Êó∂Èó¥ÈÄâÊã©ÂèòÊõ¥:', e.detail.value) // Ë∞ÉËØï‰ø°ÊÅØ
				this.datetime = e.detail.value
			},
			
			onDateChange(e) {
				console.log('Êó•ÊúüÈÄâÊã©ÂèòÊõ¥:', e.detail.value)
				this.dateValue = e.detail.value
				this.updateDateTime()
			},
			
			onTimeChange(e) {
				console.log('Êó∂Èó¥ÈÄâÊã©ÂèòÊõ¥:', e.detail.value)
				this.timeValue = e.detail.value
				this.updateDateTime()
			},
			
			updateDateTime() {
				if (this.dateValue && this.timeValue) {
					this.datetime = `${this.dateValue} ${this.timeValue}`
					console.log('ÂêàÂπ∂ÂêéÁöÑÊó∂Èó¥:', this.datetime)
				}
			},
			
			formatDateTime(datetime) {
				if (!datetime) return ''
				const date = new Date(datetime)
				const year = date.getFullYear()
				const month = (date.getMonth() + 1).toString().padStart(2, '0')
				const day = date.getDate().toString().padStart(2, '0')
				const hours = date.getHours().toString().padStart(2, '0')
				const minutes = date.getMinutes().toString().padStart(2, '0')
				return `${year}-${month}-${day} ${hours}:${minutes}`
			},
			
			formatDatetimeForPicker(date) {
				const year = date.getFullYear()
				const month = (date.getMonth() + 1).toString().padStart(2, '0')
				const day = date.getDate().toString().padStart(2, '0')
				const hours = date.getHours().toString().padStart(2, '0')
				const minutes = date.getMinutes().toString().padStart(2, '0')
				// uni-app pickerÈúÄË¶ÅËøôÁßçÊ†ºÂºèÔºöYYYY-MM-DD HH:mm
				return `${year}-${month}-${day} ${hours}:${minutes}`
			},
			
			formatDateForPicker(date) {
				const year = date.getFullYear()
				const month = (date.getMonth() + 1).toString().padStart(2, '0')
				const day = date.getDate().toString().padStart(2, '0')
				return `${year}-${month}-${day}`
			},
			
			formatTimeForPicker(date) {
				const hours = date.getHours().toString().padStart(2, '0')
				const minutes = date.getMinutes().toString().padStart(2, '0')
				return `${hours}:${minutes}`
			},
			
			getMinDate() {
				// ËÆæÁΩÆÊúÄÊó©Êó∂Èó¥‰∏∫1Âπ¥Ââç
				const date = new Date()
				date.setFullYear(date.getFullYear() - 1)
				return this.formatDatetimeForPicker(date)
			},
			
			getMaxDate() {
				// ËÆæÁΩÆÊúÄÊôöÊó∂Èó¥‰∏∫ÂΩìÂâçÊó∂Èó¥
				const date = new Date()
				return this.formatDatetimeForPicker(date)
			},
			
			saveRecord() {
				if (!this.canSave) {
					uni.showToast({
						title: 'ËØ∑Â°´ÂÜôÂÆåÊï¥‰ø°ÊÅØ',
						icon: 'none'
					})
					return
				}
				
				if (this.isEditMode) {
					// ÁºñËæëÊ®°ÂºèÔºöÊõ¥Êñ∞Áé∞ÊúâËÆ∞ÂΩï
					this.updateRecord()
				} else {
					// Êñ∞Â¢ûÊ®°ÂºèÔºöÂàõÂª∫Êñ∞ËÆ∞ÂΩï
					this.createRecord()
				}
			},
			
			// ÂàõÂª∫Êñ∞ËÆ∞ÂΩï
			createRecord() {
				const record = {
					id: Date.now().toString(),
					type: this.currentType,
					amount: this.moneyCalculator.format(parseFloat(this.amount)),
					categoryId: this.selectedCategory.id,
					categoryName: this.selectedCategory.name,
					categoryIcon: this.selectedCategory.icon,
					note: this.note.trim(),
					time: new Date(this.datetime).toISOString()
				}
				
				// ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
				const records = uni.getStorageSync('records') || []
				records.push(record)
				uni.setStorageSync('records', records)
				
				// ÊòæÁ§∫ÊàêÂäüÊèêÁ§∫
				uni.showToast({
					title: 'ËÆ∞ÂΩïÂ∑≤‰øùÂ≠ò',
					icon: 'success'
				})
				
				// ËøîÂõûÈ¶ñÈ°µ
				setTimeout(() => {
					uni.navigateBack()
				}, 1500)
			},
			
			// Êõ¥Êñ∞Áé∞ÊúâËÆ∞ÂΩï
			updateRecord() {
				const records = uni.getStorageSync('records') || []
				const recordIndex = records.findIndex(item => item.id == this.editingRecordId)
				
				if (recordIndex == -1) {
					uni.showToast({
						title: 'ËÆ∞ÂΩï‰∏çÂ≠òÂú®',
						icon: 'error'
					})
					return
				}
				
				// Êõ¥Êñ∞ËÆ∞ÂΩïÊï∞ÊçÆ
				const updatedRecord = {
					...records[recordIndex], // ‰øùÊåÅÂéüÊúâÁöÑidÂíåÂàõÂª∫Êó∂Èó¥Á≠â‰ø°ÊÅØ
					type: this.currentType,
					amount: this.moneyCalculator.format(parseFloat(this.amount)),
					categoryId: this.selectedCategory.id,
					categoryName: this.selectedCategory.name,
					categoryIcon: this.selectedCategory.icon,
					note: this.note.trim(),
					time: new Date(this.datetime).toISOString()
				}
				
				records[recordIndex] = updatedRecord
				uni.setStorageSync('records', records)
				
				// ÊòæÁ§∫ÊàêÂäüÊèêÁ§∫
				uni.showToast({
					title: 'ËÆ∞ÂΩïÂ∑≤Êõ¥Êñ∞',
					icon: 'success'
				})
				
				// ËøîÂõûÈ¶ñÈ°µ
				setTimeout(() => {
					uni.navigateBack()
				}, 1500)
			}
		}
	}
</script>

